import { all, fork } from 'redux-saga/effects'

import accountsSaga from './accountsSaga'
import accountBalancesSaga from './accountBalancesSaga'
// import blocksSaga from './blocksSaga'
// import contractsSaga from './contractsSaga'
// import dappStatusSaga from './dappStatusSaga'
// import web3Saga from './web3Saga'

export default function * root () {
  yield all([
    fork(accountsSaga),
    fork(accountBalancesSaga),
    // fork(blocksSaga),
    // fork(contractsSaga),
    // fork(dappStatusSaga),
    // fork(web3Saga)
  ])
}



