import initialState from '../initialState'
import { accounts as types } from '../constants/actionTypes'

const defaultState = initialState.accounts

export default (state = defaultState, action) => {
  switch (action.type) {
    case types.ACCOUNTS_FETCHING:
      return state
    case types.ACCOUNTS_FETCHED:
      return action.payload
    default:
      return state
  }
}



