import * as types from '../actionTypes/accounts'

export const accountsFetching = results => ({
  type: types.ACCOUNTS_FETCHING,
  payload: results
})

export const accountsFetched = results => ({
  type: types.ACCOUNTS_FETCHED,
  payload: results
})

export const accountsFailed = error => ({
  type: types.ACCOUNTS_FAILED,
  payload: error
})



